import { distance2D } from "./ForceDirectedGraph/distance"
import { uniq } from "ramda"
import Point2D from "./ForceDirectedGraph/Point2D"
import { Address, Distance2D } from "./ForceDirectedGraph/types.d"

const NEIGHBORS = [
  { row: 1, col: 0 },
  { row: 2, col: 0 },
  { row: 1, col: -1 },
  { row: -1, col: -1 },
  { row: -2, col: 0 },
  { row: -1, col: 0 },
]

export const getNeighbor = (
  col: number,
  row: number,
  neighborIndex: number,
  THETA_POSITIONS: number,
  PHI_POSITIONS: number
): Address => {
  const neighbor = NEIGHBORS[neighborIndex]

  const newRow = row + neighbor.row
  const newCol = col + neighbor.col

  return {
    row: newRow < 0 ? newRow + PHI_POSITIONS : newRow % PHI_POSITIONS,
    col: newCol < 0 ? newCol + THETA_POSITIONS : newCol % THETA_POSITIONS,
  }
}

export const buildPositionMatrix = (
  THETA_POSITIONS: number,
  PHI_POSITIONS: number
): Point2D[][] => {
  const _THETA_DIVISIONS = 3 * THETA_POSITIONS

  const THETA_INTERVAL = (2 * Math.PI) / _THETA_DIVISIONS
  const PHI_INTERVAL = (2 * Math.PI) / PHI_POSITIONS

  let layer = 0
  const positionMatrix = []

  for (let row = 0; row < PHI_POSITIONS; row++) {
    positionMatrix[row] = Array(THETA_POSITIONS)

    const thetaOffset = layer % 2 === 1 ? 1.5 * THETA_INTERVAL : 0

    for (let col = 0; col < THETA_POSITIONS; col++) {
      positionMatrix[row][col] = new Point2D(
        thetaOffset + 3 * col * THETA_INTERVAL,
        row * PHI_INTERVAL
      )
    }

    layer++
  }

  return positionMatrix
}
