export const buildEmptyPlacements = (
  THETA_POSITIONS: number,
  PHI_POSITIONS: number
): object[][] => {
  const empties = []

  for (let i = 0; i < PHI_POSITIONS; i++) {
    empties[i] = Array(THETA_POSITIONS)
    for (let j = 0; j < THETA_POSITIONS; j++) {
      empties[i][j] = null
    }
  }

  return empties
}

const getPrecomputedDistance = (
  c1: Address,
  c2: Address,
  distanceMatrix: Distance2D[][][][]
): Distance2D => distanceMatrix[c1.row][c1.col][c2.row][c2.col]

export const assignItemsToNearestAddress = (
  items,
  areaSize,
  addressCoordMatrix,
  THETA_POSITIONS,
  PHI_POSITIONS
) => {
  const placements = buildEmptyPlacements(THETA_POSITIONS, PHI_POSITIONS)

  items.forEach(item => {
    const { position } = item

    let minDistance = { magnitude: Number.MAX_SAFE_INTEGER }
    let closestHexagon = null,
      closestAddress = null

    addressCoordMatrix.forEach((rowArray, row) => {
      rowArray.forEach((coords, col) => {
        if (!placements[row][col]) {
          const distance = distance2D(position, coords, areaSize)

          if (distance.magnitude < minDistance.magnitude) {
            minDistance = distance
            closestHexagon = coords
            closestAddress = { row, col }
          }
        }
      })
    })

    if (closestHexagon) {
      item.position = new Point2D(closestHexagon.x, closestHexagon.y)
      placements[closestAddress.row][closestAddress.col] = item
    }
  })
}
