import React, { useState } from "react"

const angleDiff = (destinationAngle, sourceAngle) => {
  var distance = (destinationAngle - sourceAngle) % 360
  if (distance < -180) distance += 360
  else if (distance > 179) distance -= 360
  return distance
}

export const Motion = ({ onChangeOrientation }) => {
  const [startX, setStartX] = useState()
  const [startY, setStartY] = useState()

  const handleOrientation = event => {
    console.log("hme")
    if (startX === undefined) {
      setStartX(event.beta)
      setStartY(event.gamma)
    } else {
      onChangeOrientation({
        x: angleDiff(event.beta, startX),
        y: angleDiff(event.gamma, startY),
      })

      setStartX(0.95 * startX + 0.05 * event.beta)
      setStartY(0.95 * startY + 0.05 * event.gamma)
    }
  }

  const checkDeviceEvents = () => {
    if (typeof DeviceOrientationEvent.requestPermission === "function") {
      DeviceOrientationEvent.requestPermission()
        .then(permissionState => {
          if (permissionState === "granted") {
            window.addEventListener(
              "deviceorientation",
              handleOrientation,
              true
            )
          }
        })
        .catch(console.error)
    } else {
      window.addEventListener("deviceorientation", handleOrientation, true)
    }
  }

  return (
    <div style={{ position: "absolute", zIndex: 1000 }}>
      <button type="button" onClick={checkDeviceEvents}>
        track
      </button>
      <span style={{ margin: 2, color: "#fff" }}>x{parseInt(startX)}</span>
      <span style={{ margin: 2, color: "#fff" }}>y{parseInt(startY)}</span>
    </div>
  )
}
